export const historyKey = "history";

export const restoreState = (key: string) => {
  let history = localStorage.getItem(key);
  if (history) {
    return JSON.parse(history);
  }
};

export const saveState = (key: string, request: string) => {
  let requestsAsString = localStorage.getItem(historyKey);
  if (requestsAsString) {
    const history: Array<string> = JSON.parse(requestsAsString);
    if (history.length > 4) {
      history.pop();
    }
    history.unshift(request);
    localStorage.setItem(key, JSON.stringify(history));
  } else {
    const history: Array<string> = [request];
    localStorage.setItem(key, JSON.stringify(history));
  }
};
