import React, { useEffect } from "react";
import { RepoContainer } from "./RepoContainer/RepoContainer";
import { SearchForm } from "./Search/SearchForm";
import "./MainPage.scss";
import { useDispatch, useSelector } from "react-redux";
import { AppStateType } from "../store/store";
import { Loading } from "./Loading/Loading";
import { historyKey, restoreState } from "../store/localStorage";
import { gitSearchActions } from "../store/gitRepo/gitRepoActions";

export const MainPage = () => {
  const { gitRepo, history, status } = useSelector(
    (state: AppStateType) => state.gitRepo
  );

  const dispatch = useDispatch();

  useEffect(() => {
    const history = restoreState(historyKey);
    if (history) dispatch(gitSearchActions.restoreHistory(history));
  }, [dispatch]);

  return (
    <div className="main-container">
      <SearchForm history={history} />
      {status === "loading" ? <Loading /> : <RepoContainer gitRepo={gitRepo} />}
    </div>
  );
};
